import produce from 'immer';
import cloneDeepWith from 'lodash/cloneDeepWith';

import { actionTypes } from './actions';
import filter from './filter';

const initialState = cloneDeepWith(
  {
    data: {
      records: [
        {
          name: 'AB Ceed',
          role: 'engineer',
          connectedOn: new Date('2019-01-01'),
          status: 'screen',
        },
        {
          name: 'John Smith',
          role: 'sales',
          connectedOn: new Date('2019-01-02'),
          status: 'explored',
        },
        {
          name: 'A.B.C.',
          role: 'customerSupport',
          connectedOn: new Date('2019-01-03'),
          status: 'hire',
        },
        {
          name: 'D. E. F.',
          role: 'manager',
          connectedOn: new Date('2019-01-03'),
          status: 'scheduled',
        },
        {
          name: 'Master of Engine',
          role: 'manager',
          connectedOn: new Date('2019-01-05'),
          status: 'screen',
        },
        {
          name: 'Donald Scrooge',
          role: 'engineer',
          connectedOn: new Date('2019-01-07'),
          status: 'expored',
        },
      ],
      statuses: ['screen', 'explored', 'hire', 'scheduled'],
      roles: ['engineer', 'sales', 'customerSupport', 'manager'],
    },
    view: {
      keys: [],
      records: {},
      search: '',
      statuses: {
        // each key is a status, each value is a counter
      },
    },
  },
  value => {
    if (value && Array.isArray(value.records)) {
      value.records = value.records.map(({ connectedOn, ...record }, index) => ({
        id: String(index),
        ...record,
        connectedOn: connectedOn.toISOString(),
      }));
      return value;
    }
  },
);

const updateView = draft => {
  const { search } = draft.view;
  const { records } = draft.data;
  draft.view.records = filter(records)(search).reduce((acc, record) => {
    acc[record.id] = record;
    return acc;
  }, {});
  draft.view.statuses = draft.data.statuses.reduce((acc, status) => {
    acc[status] = Object.values(draft.view.records).filter(record => record.status === status).length;
    return acc;
  }, {});
  if (!draft.view.keys.length) {
    const keySet = new Set(records.map(record => Object.keys(record)).flat());
    keySet.delete('id');
    draft.view.keys = [...keySet];
  }
};

export default (state = initialState, { type, payload }) => {
  const getState = {
    [actionTypes.INIT]: draft => {
      updateView(draft);
    },
    [actionTypes.SEARCH]: draft => {
      const { search } = payload;
      draft.view.search = search;
      updateView(draft);
    },
    [actionTypes.STATUS]: draft => {
      const { id, status } = payload;
      const index = parseInt(id, 10);
      const { records, statuses } = state.data;
      if (index > records.length || index < 0) {
        throw new Error(`Invalid index '${index}'. It should be lower than ${records.length}`);
      }
      if (!statuses.includes(status)) {
        throw new Error(`Invalid status '${status}'. It should be one of ${JSON.stringify(statuses)}`);
      }
      draft.data.records[index].status = status;
      updateView(draft);
    },
  }[type];
  return (getState && produce(state, getState)) || state;
};
