import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';

import { actionTypes } from './actions';

function RecordsStatus({ id, value, statuses, onStatus }) {
  return (
    <FormControl>
      <NativeSelect value={value} onChange={e => onStatus({ id, status: e.target.value })}>
        {statuses.map(status => (
          <option key={status} value={status}>
            {status}
          </option>
        ))}
      </NativeSelect>
    </FormControl>
  );
}

RecordsStatus.propTypes = {
  id: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  statuses: PropTypes.arrayOf(PropTypes.string).isRequired,
  onStatus: PropTypes.func.isRequired,
};

export default connect(
  state => state.data,
  dispatch => ({
    onStatus: ({ id, status }) => dispatch({ type: actionTypes.STATUS, payload: { id, status } }),
  }),
)(RecordsStatus);
