import React, { useState } from 'react';
import debounce from 'lodash/debounce';
import memoize from 'lodash/memoize';
import PropTypes from 'prop-types';

import View from './View';

const slowdown = memoize(debounce);

function RecordsSearchContainer({ search: initialSearch, onChange }) {
  const [search, setSearch] = useState(initialSearch);
  const onSearch = slowdown(onChange, 200);
  const handleEvent = e => {
    const newSearch = e.target.value || '';
    setSearch(newSearch);
    onSearch(newSearch);
  };
  return <View search={search} onEvent={handleEvent} />;
}

RecordsSearchContainer.propTypes = {
  search: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
};

export default RecordsSearchContainer;
