import React from 'react';
import PropTypes from 'prop-types';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import ClearIcon from '@material-ui/icons/Clear';

function RecordsSearchView({ search, onEvent }) {
  return (
    <Paper style={{ padding: 16 }}>
      <div style={{ overflow: 'hidden' }}>
        <div style={{ float: 'left' }}>
          {search.length ? <ClearIcon onClick={onEvent} style={{ cursor: 'pointer' }} /> : <SearchIcon />}
        </div>
        <InputBase
          style={{ float: 'left', width: 'calc(100% - 50px)' }}
          placeholder="Searchâ€¦"
          value={search}
          onChange={onEvent}
        />
      </div>
    </Paper>
  );
}

RecordsSearchView.propTypes = {
  search: PropTypes.string.isRequired,
  onEvent: PropTypes.func.isRequired,
};

export default RecordsSearchView;
