import React, { memo } from 'react';
import TableRow from '@material-ui/core/TableRow';

import TableCell from './TableCell';
import Status from './Status';
import TablePropTypes from './tablePropTypes';

function RecordsTabelRow({ columns, row }) {
  return (
    <TableRow hover role="checkbox" tabIndex={-1}>
      {columns.map(({ id }) => {
        const value = row[id];
        return (
          <TableCell key={id} align="left">
            {id === 'status' ? <Status id={row.id} value={value} /> : value}
          </TableCell>
        );
      })}
    </TableRow>
  );
}

RecordsTabelRow.propTypes = {
  columns: TablePropTypes.columns.isRequired,
  row: TablePropTypes.row.isRequired,
};

export default memo(RecordsTabelRow);
