import React, { useEffect } from 'react';
import memoize from 'lodash/memoize';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { actionTypes } from './actions';
import Search from './Search';
import Stat from './Stat';
import Table from './Table';

const getColumns = memoize(keys => keys.map(key => ({ id: key, label: key })));

function Records({ search, statuses, keys, records, onInit, onSearch }) {
  useEffect(() => {
    onInit();
  }, []);
  return (
    <>
      <Search search={search} onChange={onSearch} />
      <Stat statuses={statuses} />
      <Table columns={getColumns(keys)} rows={records} />
    </>
  );
}

Records.propTypes = {
  keys: PropTypes.arrayOf(PropTypes.string).isRequired,
  records: PropTypes.objectOf(PropTypes.shape({})).isRequired,
  statuses: PropTypes.objectOf(PropTypes.number).isRequired,
  search: PropTypes.string.isRequired,
  onInit: PropTypes.func.isRequired,
  onSearch: PropTypes.func.isRequired,
};

export default connect(
  state => state.view,
  dispatch => ({
    onSearch: search => dispatch({ type: actionTypes.SEARCH, payload: { search } }),
    onInit: () => dispatch({ type: actionTypes.INIT }),
  }),
)(Records);
