import memoize from 'lodash/memoize';

const match = memoize(({ name, role, status }) =>
  memoize(search =>
    [name, role, status].find(value =>
      value
        .toLowerCase()
        .trim()
        .replace(/[^a-z0-9]+/gim, ' ')
        .includes(search))));

export default memoize(records =>
  memoize(search => {
    const normalized = search
      .toLowerCase()
      .trim()
      .replace(/\s+/gm, ' ');
    const sections = normalized.split(/\s/gm);
    const or = sections.length === 3 && sections[1] === 'or';
    const and = sections.length === 3 && sections[1] === 'and';
    if (or) {
      return records.filter(record => match(record)(sections[0]) || match(record)(sections[2]));
    }
    if (and) {
      return records.filter(record => match(record)(sections[0]) && match(record)(sections[2]));
    }
    return records.filter(record => match(record)(normalized));
  }));
