import PropTypes from 'prop-types';

const row = PropTypes.shape({
  id: PropTypes.string.isRequired,
});

export default {
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      label: PropTypes.string.isRequired,
    }),
  ),
  rows: PropTypes.objectOf(row),
  row,
};
